import React from "react"
import { graphql } from "gatsby"
import LayoutAddon from "../components/layoutAddon"
import SEO from "../components/seo"
// import Img from "gatsby-image"
export default function Blogpost({ data }) {
  return (
    <LayoutAddon>
      <SEO title={`${data.contentstackGatsbyBlockpost.title}`} />
      <div>
        <h2>Welcome to {data.contentstackGatsbyBlockpost.title}</h2>
        <p>{data.contentstackGatsbyBlockpost.blog_content}</p>
        <img
          title="Header image"
          alt={data.contentstackGatsbyBlockpost.blog_images.title}
          src={`${data.contentstackGatsbyBlockpost.blog_images.url}?format=webp`}
          style={{width:"500vw"}}
        />
        <p>{data.contentstackGatsbyBlockpost.blog_content}</p>
      </div>
    </LayoutAddon>
  )
}
export const postQuery = graphql`
  query allcontentstackGatsbyBlockpost($title: String!) {
    contentstackGatsbyBlockpost(title: { eq: $title }) {
      title
      blog_content
      blog_images {
        url
        title
      }
    }
  }
`
